<template>
<div class="container">
    <div class="body"></div>
    <b-alert :show="dismissCountDown" dismissible :variant="mensaje.color" @dismissed="dismissCountDown = 0" @dismiss-count-down="countDownChanged">
        {{ mensaje.texto }}
    </b-alert>
    <div class="grad"></div>
    <div class="header">
        <div>Evalua<span>Tech</span></div>
    </div>
    <br />
    <div class="login">
        <form @submit.prevent="ingresar()">
            <input type="text" placeholder="Usuario" name="user" v-model="ingreso.usuario" required />
            <input type="password" placeholder="Contraseña" name="password" v-model="ingreso.clave" required />
            <!-- <input type="button" value="Iniciar"> -->
            <button type="submit">Iniciar</button>
        </form>

        <!-- <input type="button" value="Registrarse"> -->
        <!-- <router-link id="rutahome" to="/inicio"></router-link> -->
        <!-- <button type="button"><a href="./">Registrarse</a></button> -->
        <!-- <a href="/registro"><button type="button">Registrarse</button></a> -->
        <router-link id="i" to="/registro"><button type="button">Registrarse</button></router-link>
    </div>
</div>
</template>

<script>
export default {
    data() {
        return {
            docentes: [],
            estudiantes: [],
            dismissSecs: 5,
            dismissCountDown: 0,
            mensaje: {
                color: "",
                texto: ""
            },
            ingreso: {
                usuario: "",
                vlave: "",
            },
        };
    },
    methods: {
        lista_docentes() {
            this.axios
                .get("/docente")
                .then((res) => {
                    this.docentes = res.data;
                })
                .catch((e) => {
                    console.log(e.response);
                    this.showAlert();
                });
        },
        lista_estudiantes() {
            this.axios
                .get("/estudiante")
                .then((res) => {
                    this.estudiantes = res.data;
                })
                .catch((e) => {
                    console.log(e.response);
                    this.showAlert();
                });
        },
        ingresar() {

            let valido = false;
            let valido2 = false;
            let idDoc;
            let idEst;
            this.lista_docentes();
            this.lista_estudiantes();
            setTimeout(() => {
                for (let i = 0; i < this.docentes.length; i++) {
                    if (this.docentes[i].usuario === this.ingreso.usuario) {
                        if (this.docentes[i].clave === this.ingreso.clave) {
                            valido = true;
                            idDoc = this.docentes[i]._id;
                        }
                    }
                }

                for (let i = 0; i < this.estudiantes.length; i++) {
                    if (this.estudiantes[i].identificacion === this.ingreso.usuario) {
                        if (this.estudiantes[i].clave === this.ingreso.clave) {
                            valido2 = true;
                            idEst = this.estudiantes[i]._id;
                        }
                    }
                }
                if (valido) {
                    //alert("Soy Docente");
                    // window.location.href = "http://localhost:8080/inicio";
                    this.$router.push('/vistaDocente/');
                    localStorage.setItem("idDoc", idDoc);

                    //this.$router.push('/vistaDocente/' + idDoc);
                    //this.$router.append(idDoc);

                } else if (valido2) {
                    //alert("Soy Estudiante");
                    //window.location.href = "http://localhost:8080/evaluacionE";
                    this.$router.push('/vistaEstudiante/');
                    localStorage.setItem("idEst", idEst);
                    //this.$router.append(idDoc);

                } else {
                    this.ingreso.usuario = "";
                    this.ingreso.clave = "";
                    this.mensaje.color = "danger";
                    this.mensaje.texto = "Credenciales invalidas";
                    this.showAlert();
                }
            }, 1000);
        },
        countDownChanged(dismissCountDown) {
            this.dismissCountDown = dismissCountDown;
        },
        showAlert() {
            this.dismissCountDown = this.dismissSecs;
        },
    },
};
</script>

